<template>
  <div class="showcase-page page" :id="id" v-bind:style="{height: height}">
    <div class="showcase-container">
      <h1 class="hidden" :id="id+'-title'">{{ title }}</h1>
      <transition-group name="showcaseitem" class="items-container" :id="id+'-items-container'">
        <ShowcaseItem 
          v-for="item in items_instance"
          :key="item.name"
          :item="item"
          :id="item.name"
          :imageSrc="item.image"
          v-on:click.native="SelectItem(item)" />
      </transition-group>
    </div>
  </div>
</template>

<script>
import ShowcaseItem from './ShowcaseItem'

export default {
  name: "Showcase",
  props: {
    title: String,
    id: String,
    items: Array,
    height: String
  },
  components: {
    "ShowcaseItem": ShowcaseItem
  },
  data() {
    return {
      items_instance: [],
      expanded: false,
      selectedItem: null,
    }
  },
  methods: {
    /*
    GetImageURL(path) {
      // depracated function. now using imgur for images
      // instead of storing locally
      return require("@/assets/imgs/" + path);
    },*/
    GetHeight() {
      return this.items.length * 142;
    },
    ShowItems(expansionRate) {
      if(this.expanded)
        return;

      document.getElementById(`${this.id}-title`).classList.remove("hidden");

      var i = 0;
      var expansionInterval = setInterval(() => {
        this.items_instance.push(this.items[i]);

        if(i >= this.items.length - 1) {
          clearInterval(expansionInterval);
          this.expanded = true;
        }
        i++;
      }, expansionRate);
    },
    SelectItem(item) {
      document.getElementsByClassName("selected-showcase-item").forEach(element => {
          element.classList.remove("selected-showcase-item");
      });
      document.getElementsByClassName("awards").forEach(element => {
          element.classList.add("hidden");
      })
      document.getElementsByClassName("links").forEach(element => {
          element.classList.add("hidden");
      })

      document.getElementById(item.name).classList.add("selected-showcase-item");

      if(document.getElementById(`${item.name}-awards`).childElementCount != 0)
          document.getElementById(`${item.name}-awards`).classList.remove("hidden");
      document.getElementById(`${item.name}-links`).classList.remove("hidden");

      // remove padding on all showcase elements, then 
      document.getElementsByClassName("showcase-page").forEach(element => {
        element.style.paddingBottom = "";
      })
      // should be roughly the size of an increased selected
      var showcaseItemHeight = 130;
      var selectedShowcaseItemHeight = 393;
      document.getElementById(this.id).style.paddingBottom = `${selectedShowcaseItemHeight - showcaseItemHeight}px`;
    }
  },
};
</script>

