   mounted() {
      // dictionary ?
      var pages = this.pages;
      var pageHeights = [];
      var cumulativeHeights = [];

      pages.forEach((page) => {
         pageHeights.push(document.getElementById(page).clientHeight);
      });

      cumulativeHeights.push(pageHeights[0]);
      for (var i = 1; i < pageHeights.length; i++) {
         cumulativeHeights.push(pageHeights[i] + cumulativeHeights[i - 1]);
      }

      // update the 'selected' nav when scroll changes
      document.onscroll = () => {
         var scroll = window.scrollY || window.scrollTop;
         // for each page if we get to around the page, set as selected

         pages.forEach((page) => {
            /*
        var cumulativeHeight = 0;
        for(var i = 0; i < pages.indexOf(page); i++) {
          cumulativeHeight += pageHeights[i];
        }*/
            // if we scroll to a certain page
            if (
               scroll >=
               cumulativeHeights[pages.indexOf(page) - 1] -
                  document.getElementById(page).clientHeight / 4
            ) {
               // remove selected if it has it
               document
                  .getElementsByClassName("selected")
                  .forEach((element) => {
                     // this removes entire list
                     // depracated
                     // element.classList.remove(...element.classList);
                     element.classList.remove("selected");
                  });
               // assign the selected nav element
               document.getElementById(`${page}-nav`).classList.add("selected");
               // add the 'visible' classlist to fade in as we scroll down
               document.getElementById(page).classList.add("visible");
               // for Showcase pages, activate their "ShowItems" function
               // takes a number as the delay between showing each
               // element in ms
               if (page == "games" || page == "apps" || page == "videos") {
                  if (!this.$refs[page].expanded) {
                     this.$refs[page].ShowItems(150);
                     this.$refs[page].expanded = true;
                  }
               }
            } else if (scroll == undefined || scroll < 100) {
               // we've reached the top
               // remove selected if it has it
               document
                  .getElementsByClassName("selected")
                  .forEach((element) => {
                     // this removes entire list
                     // depracated
                     // element.classList.remove(...element.classList);
                     element.classList.remove("selected");
                  });
               // assign the selected nav element
               document.getElementById(`home-nav`).classList.add("selected");
               // add the 'visible' classlist to fade in as we scroll down
               document.getElementById("home").classList.add("visible");
            }
         });
      };
   },
   methods: {
      ScrollToPage(_pageID) {
         document.getElementById(_pageID).scrollIntoView({
            behavior: "smooth",
         });
      },
   },
};
</script>

